<template>
  <div id="app">
    <div class="container">
      <div class="row">
        <div class="col-xs-12">
          <div class="item">
            <div class="row location">
              <div class="col-lg-6 col-md-6 col-sm-6 col-xs-12">
                <div class="input-group mb-10">
                  <div class="input-group-btn">
                    <button type="button" class="btn btn-info dropdown-toggle" data-toggle="dropdown"
                            aria-haspopup="true"
                            aria-expanded="false">Belgium <span class="caret"></span></button>
                    <ul class="dropdown-menu">
                      <li><a href="#">Poland</a></li>
                      <li><a href="#">USA</a></li>
                      <li><a href="#">GB</a></li>
                    </ul>
                  </div>
                  <input type="text" class="form-control" aria-label="..." placeholder="Zip code, Address">
                </div>
              </div>
              <div class="col-lg-6 col-md-6 col-sm-6 col-xs-12">
                <div class="input-group mb-10">
                  <div class="input-group-btn">
                    <button type="button" class="btn btn-info dropdown-toggle" data-toggle="dropdown"
                            aria-haspopup="true"
                            aria-expanded="false">Belgium <span class="caret"></span></button>
                    <ul class="dropdown-menu">
                      <li><a href="#">Poland</a></li>
                      <li><a href="#">USA</a></li>
                      <li><a href="#">GB</a></li>
                    </ul>
                  </div>
                  <input type="text" class="form-control" aria-label="..." placeholder="Zip code, Address">
                </div>
              </div>
            </div>
            <div class="row flex-align type" v-for="(item, index) in items">
              <div class="col-lg-3 col-md-3 col-sm-12 col-xs-12 mb-10">
                <bootstrap-toggle v-model="item.parcel":options="{ on: 'PARCEL', off: 'PALLET', offstyle: 'primary' }"/>
              </div>
              <div class="col-lg-8 col-md-8 col-lg-offset-1 col-sm-12 col-xs-12" v-if="item.parcel">
                <div class="col-lg-3 col-md-6 col-sm-6 col-xs-12">
                  <label class="text-center">Weight:
                    <div class="input-group">
                      <input type="text" class="form-control" placeholder="" aria-describedby="basic-addon2"
                             v-model="item.parcelWeight">
                      <span class="input-group-addon" id="basic-addon2">Kg</span>
                    </div>
                  </label>
                </div>
                <div class="col-lg-3 col-md-6 col-sm-6 col-xs-12">
                  <label class="text-center">Length:
                    <div class="input-group">
                      <input type="text" class="form-control" placeholder="" aria-describedby="basic-addon2"
                             v-model="item.parcelLength">
                      <span class="input-group-addon" id="basic-addon2">Cm</span>
                    </div>
                  </label>
                </div>
                <div class="col-lg-3 col-md-6 col-sm-6 col-xs-12">
                  <label class="text-center">Width:
                    <div class="input-group">
                      <input type="text" class="form-control" placeholder="" aria-describedby="basic-addon2"
                             v-model="item.parcelWidth">
                      <span class="input-group-addon" id="basic-addon2">Cm</span>
                    </div>
                  </label>
                </div>
                <div class="col-lg-3 col-md-6 col-sm-6 col-xs-12">
                  <label class="text-center">Height:
                    <div class="input-group">
                      <input type="text" class="form-control" placeholder="" aria-describedby="basic-addon2"
                             v-model="item.parcelHeight">
                      <span class="input-group-addon" id="basic-addon2">Cm</span>
                    </div>
                  </label>
                </div>
              </div>
              <div class="col-lg-8 col-lg-offset-1 col-sm-12 col-xs-12" v-if="!item.parcel">
                <div class="col-lg-4 col-md-4 col-sm-4 col-xs-12">
                  <label class="text-center">Number of pallets:
                    <div class="input-group">
                      <input type="text" class="form-control" placeholder="" aria-describedby="basic-addon2"
                             v-model="item.palletNumber">
                      <span class="input-group-addon" id="basic-addon2">Pcs</span>
                    </div>
                  </label>
                </div>
                <div class="col-lg-4 col-md-4 col-sm-4 col-xs-12">
                  <label class="text-center">Height:
                    <div class="input-group">
                      <input type="text" class="form-control" placeholder="" aria-describedby="basic-addon2"
                             v-model="item.palletHeight">
                      <span class="input-group-addon" id="basic-addon2">Cm</span>
                    </div>
                  </label>
                </div>
                <div class="col-lg-4 col-md-4 col-sm-4 col-xs-12">
                  <label class="text-center">Weight for pallet:
                    <div class="input-group">
                      <input type="text" class="form-control" placeholder="" aria-describedby="basic-addon2"
                             v-model="item.palletWeight">
                      <span class="input-group-addon" id="basic-addon2">Kg</span>
                    </div>
                  </label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-xs-12 controls">
          <div class="left">
            <button class="btn-primary btn mb-10" @click="removeItem()">- Remove</button>
            <button class="btn-primary btn mb-10" @click="addItem">+ Add parcel of pallet</button>
          </div>
          <div class="right">
            <button class="btn-danger btn mb-10" @click="sendData()">Quote & Book</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import BootstrapToggle from 'vue-bootstrap-toggle'

    export default {
        data () {
            return {
                items: [{parcel: true}]
            }
        },
        methods: {
            addItem() {
                this.items.push({parcel: false});
            },
            removeItem() {
                this.items.pop()
            },
            sendData () {
                console.log(this.items)
            }
        },
        components: {BootstrapToggle}
    }
</script>

<style src="../node_modules/bootstrap/dist/css/bootstrap.css"></style>

<style lang="scss">
  html,
  body {
    background-color: #0e8fd5;
  }

  #app {
    padding-top: 135px;
    padding-bottom: 135px;
  }

  .item {
    border-radius: 10px;
    padding: 30px 120px 20px 120px;
    margin-bottom: 10px;
    background-color: white;

  @media screen and (max-width: 992px) {
    padding: 30px 60px 20px 60px;
  }

  }

  .location,
  .type {
    margin-bottom: 15px;
  }

  .flex-align {
    display: flex;
    align-items: flex-end;

  @media screen and (max-width: 992px) {
    flex-direction: column;
  }

  }

  .controls {

  .left {
    float: left;
  }

  .right {
    float: right;

  @media screen and (max-width: 500px) {
    float: left;
  }

  }

  .btn {
    border-radius: 0;

  padding: {
    left: 30px;
    right: 30px;
  };
  }
  }

  .mb-10 {
    margin-bottom: 10px;
  }
</style>



